import React, { Component } from 'react';

import { Link } from 'react-router-dom';

import { 
    Col, Row,
    Card, CardBody,
    UncontrolledDropdown, DropdownMenu, DropdownToggle, DropdownItem, CustomInput
} from "reactstrap";

import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search, CSVExport } from 'react-bootstrap-table2-toolkit';
import paginationFactory from 'react-bootstrap-table2-paginator';

import LoaderWidget from '../../components/Loader';

import { fetchJSON } from "../../helpers/api";

type ProblemListProps = {
    filters: PropTypes.object
};

type ProblemListState = {};

const CustomToggleList = ({ columns, onColumnToggle, toggles }) => {
    return (
        <React.Fragment>
            <UncontrolledDropdown className="list-custom-class mb-2">
                <DropdownToggle color="link" caret>
                    <i className="mdi mdi-view-week"></i>{' '}
                </DropdownToggle>
                <DropdownMenu>
                    {columns
                        .map(column => ({
                            ...column,
                            toggle: toggles[column.dataField],
                        }))
                        .map((column, idx) => (
                            <DropdownItem key={idx} toggle={false}>
                                <CustomInput
                                    type="checkbox"
                                    id={`col-${column.text}`}
                                    label={column.text}
                                    defaultChecked={column.toggle}
                                    onClick={() => {
                                        return onColumnToggle(column.dataField);
                                    }}
                                />
                            </DropdownItem>
                        ))}
                </DropdownMenu>
            </UncontrolledDropdown>
        </React.Fragment>
    );
};

class ProblemList extends Component<ProblemListProps, ProblemListState> {
    
    constructor(props) {
        super(props);

        this.state = {
            isLoading: false,
            loaded: false,
            filters: null,
            records: [],
            columns: [
                {
                    dataField: 'companyId',
                    text: 'Compaa',
                    sort: false,
                    searchable: false,
                    hidden: true
                },
                {
                    dataField: 'initialAnalysisDate',
                    text: 'Fecha de Anlisis',
                    type: "date",
                    sort: true,
                },
                {
                    dataField: 'name',
                    text: 'Nombre',
                    sort: true,
                },
                {
                    dataField: 'problemTypeRequest.description',
                    text: 'Tipo de Problema',
                    sort: true,
                },
                {
                    dataField: 'systemCatRequest.description',
                    text: 'Sistema',
                    sort: true,
                },
                {
                    dataField: 'equipmentTypeRequest.description',
                    text: 'Tipo de Equipo',
                    sort: true,
                },
                {
                    dataField: 'installationRequest.description',
                    text: 'Instalacin',
                    sort: true,
                },
                {
                    dataField: 'active',
                    text: 'Estado',
                    sort: false,
                    searchable: false,
                    hidden: true
                },
                {
                    dataField: 'id',
                    isDummyColumn: true,
                    text: 'Action',
                    sort: false,
                    searchable: false,
                    classes: 'table-action',
                    align: 'center',
                    formatter: (cell, row, rowIndex, extraData) => {
                        
                        return (
                            <React.Fragment>
                                <Link to={"/acr/acr/"+cell} className="action-icon text-center text-primary">
                                    {' '}
                                    <i className="mdi mdi-arrow-right-thick"></i>
                                </Link>
                            </React.Fragment>
                        );
                    },
                },
            ],
            defaultSorted: [
                {
                    dataField: 'id',
                    order: 'asc',
                },
            ]
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        // check prevState
        if(JSON.stringify(prevProps.filters) !== JSON.stringify(this.props.filters)){
        
            this.GetProblems();
        }
    }

    GetProblems() {
        this.setState({isLoading: true});

        const payload = JSON.parse(JSON.stringify(this.props.filters || {active: true}));
        Object.keys(payload).forEach((key) => (payload[key] === null || payload[key] === '') && delete payload[key]);

        const options = {
            method: 'POST',
            mode: 'cors',
            cache: 'no-cache',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(payload)
        };

        let apiUrl = '/api/Problem/GetAll';

        fetchJSON(
            apiUrl, options
        ).then(
            ( result ) => {
                this.setState({loaded: true});
                this.setState({records: result.data});
                this.setState({isLoading: false});
            }
        );
    }

    render() {
        const { SearchBar } = Search;
        const { ExportCSVButton } = CSVExport;
    
        return (
            <Card>
                <CardBody>
                    <ToolkitProvider
                        bootstrap4
                        keyField="id"
                        data={this.state.records}
                        columns={this.state.columns}
                        columnToggle
                        search
                        exportCSV={{ onlyExportFiltered: true, exportAll: false }}>
                        {props => (
                            <React.Fragment>
                                <Row>
                                    <Col>
                                        <CustomToggleList {...props.columnToggleProps} />
                                    </Col>
                                    <Col sm="6">
                                        <SearchBar {...props.searchProps} placeholder="Buscar..."/>
                                    </Col>
                                    <Col className="text-right">
                                        <ExportCSVButton {...props.csvProps} className="btn btn-primary">
                                            Exportar
                                        </ExportCSVButton>
                                    </Col>
                                </Row>

                                <BootstrapTable
                                    {...props.baseProps}
                                    bordered={false}
                                    loading={ true }
                                    hover= { true }
                                    defaultSorted={this.state.defaultSorted}
                                    pagination={paginationFactory({ sizePerPage: 5 })}
                                    wrapperClasses="table-responsive"
                                    noDataIndication={ () => this.state.isLoading? <LoaderWidget /> : (this.state.loaded ? "No se encontraron problemas con los filtros seleccionados." : "Seleccione los filtros para realizar la consulta.")}
                                />
                            </React.Fragment>
                        )}
                    </ToolkitProvider>
                </CardBody>
            </Card>
        );
    }
}

export default ProblemList;