import React, { Component } from 'react';

import {
    UncontrolledPopover, PopoverHeader, PopoverBody
} from 'reactstrap';

type TooltipProps = {
    tt: object,
    target: string,
    placement: string,
    iconClass: string,
    colorClass: string,
    header: string,
    body: string
};

/**
    Tooltip class to show helper messages on form fields attached to an icon
 */
class Tooltip extends Component<TooltipProps> {

    render() {
        const tt = this.props.tt || null;

        if ( tt ) {
            const target = this.props.target + '_tt';
            const placement = this.props.placement || 'top'
            const iconClass = this.props.iconClass || (tt.iconClass? tt.iconClass : '');
            const colorClass = this.props.colorClass || (tt.colorClass? tt.colorClass : '');
            const header = this.props.header || (tt.header? tt.header : null);
            const body = this.props.body || (tt.body? tt.body : null);
            
                    
            return (
                <React.Fragment>
                    <i className={iconClass + ' ' + colorClass} id={target}>{' '}</i>
                    <UncontrolledPopover placement={placement} target={target} trigger="hover">
                        <PopoverHeader>{header}</PopoverHeader>
                        <PopoverBody>{body}</PopoverBody>
                    </UncontrolledPopover>
                </React.Fragment>
            );
        } else {
             return null;
        };
    }
};

export default Tooltip;
