import React, { Component } from 'react';

import { 
    Col, Row
} from "reactstrap";

import PageTitle from '../../components/PageTitle';
import Portlet from '../../components/Portlet';

import SearchForm from "../../components/ACR/SearchForm";
import ProblemList from "../../components/ACR/ProblemList";

class AcrList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            collapsedForm: true,
            filters: {}
        };
    }

    doFilterList = params =>{ 
        this.setState({filters: params});
    };

    render() {
        return (
            <React.Fragment>
                <PageTitle
                    breadCrumbItems={[
                        { label: 'ACR', active: true }, 
                        { label: 'Consultar', active: true }
                    ]}
                    title={ 'Consultar ACR' }
                />

                <Row>
                    <Col>
                        <Portlet 
                            title="Buscar Anlisis Causa-Razn" 
                            refreshable = { false }
                            toggleable = { true }
                            closable = { false }
                            collapsed = { this.state.collapsedForm }
                        >
                            <SearchForm doFilterList={this.doFilterList}/>
                        </Portlet>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <ProblemList filters={this.state.filters}/>
                    </Col>
                </Row>
                
            </React.Fragment>
        );
    }
};

export default AcrList;