import React, { Component } from 'react';

import {
    Row, Col,
    FormGroup,
    Label, Button,
    Input, CustomInput,
} from 'reactstrap';

import { AvForm, AvGroup, AvInput, AvFeedback } from 'availity-reactstrap-validation';

import Select from 'react-select';

import { fetchJSON } from "../../helpers/api";
import HyperDatepicker from '../../components/Datepicker';

import Tooltip from "../Tooltip";
import { searchForm } from "../../constants/tooltips/searchForm";

type SearchFormProps = {
    doFilterList: PropTypes.func
};

type SearchFormState = {};

class SearchForm extends Component<SearchFormProps, SearchFormState> {

    constructor(props) {
        super(props);

        this.state = {
            companyId:                  null,
            regionId:                   null,
            activoId:                   null,
            processCenterId:            null,
            installationId:             null,
            equipmentTypeId:            null,
            equipment:                  '',
            systemId:                   null,
            problemTypeId:              null,
            fromDate:                   null,
            toDate:                     null,

            companyIdIsValid:           true,

            isEquipmentTypeDisabled:    true,
            isSystemDisabled:           true,
            
            companies:                  [],
            regions:                    [],
            activos:                    [],
            processCenters:             [],
            installations:              [],
            equipmentTypes:             [],
            systems:                    [],
            problemTypes:               [],

            companiesIsLoading:         true,
            regionsIsLoading:           true,
            activosIsLoading:           true,
            processCentersIsLoading:    true,
            installationsIsLoading:     true,
            equipmentTypesIsLoading:    true,
            systemsIsLoading:           true,
            problemTypesIsLoading:      true,
        };
    }

    componentDidMount() {
        this.GetCatalog('company');
        this.GetCatalog('region');
        this.GetCatalog('activo');
        this.GetCatalog('processCenter');
        this.GetCatalog('installation');
        this.GetCatalog('equipmentType');
        this.GetCatalog('systemCat');
        this.GetCatalog('problemType');
    }

    GetCatalog(catName: string) {
        const options = {
            method: 'GET',
            mode: 'cors',
            cache: 'no-cache',
            headers: { 'Content-Type': 'application/json' }
        };

        let apiUrl = '/api/' + catName;

        fetchJSON(
            apiUrl, options
        ).then(
            ( result ) => {
                switch (catName) {
                    case 'company':
                        this.setState({companies: result.data})
                        this.setState({companiesIsLoading: false})
                        break;
                    case 'region':
                        this.setState({regions: result.data})
                        this.setState({regionsIsLoading: false})
                        break;
                    case 'activo':
                        this.setState({activos: result.data})
                        this.setState({activosIsLoading: false})
                        break;
                    case 'processCenter':
                        this.setState({processCenters: result.data})
                        this.setState({processCentersIsLoading: false})
                        break;
                    case 'installation':
                        this.setState({installations: result.data})
                        this.setState({installationsIsLoading: false})
                        break;
                    case 'equipmentType':
                        this.setState({equipmentTypes: result.data})
                        this.setState({equipmentTypesIsLoading: false})
                        break;
                    case 'systemCat':
                        this.setState({systems: result.data})
                        this.setState({systemsIsLoading: false})
                        break;
                    case 'problemType':
                        this.setState({problemTypes: result.data})
                        this.setState({problemTypesIsLoading: false})
                        break;
                    default:
                        // throw some error
                        break;
                }
            }
        );
    }

    toggleEquipmentType = () => {
        const isDisabled = !this.state.isEquipmentTypeDisabled;
        this.setState(state => ({ isEquipmentTypeDisabled: isDisabled }));
        
        if(isDisabled) {
            this.setState(state => ({ equipmentTypeId: null }));
        }
    }

    toggleSystem = () => {
        const isDisabled = !this.state.isSystemDisabled;
        this.setState(state => ({ isSystemDisabled: isDisabled }));
        
        if(isDisabled) {
            this.setState(state => ({ systemId: null }));
        }
    }

    handleInputChange = (selectedOption, field) => {
        switch (field) {
            case "companyId":
                this.setState({companyId: selectedOption ? selectedOption.value : null});
                this.setState({companyIdIsValid: selectedOption ? true : false});
                break;
            case "regionId":
                this.setState({regionId: selectedOption ? selectedOption.value : null});
                break;
            case "activoId":
                this.setState({activoId: selectedOption ? selectedOption.value : null});
                break;
            case "processCenterId":
                this.setState({processCenterId: selectedOption ? selectedOption.value : null});
                break;
            case "installationId":
                this.setState({installationId: selectedOption ? selectedOption.value : null});
                break;
            case "equipmentTypeId":
                this.setState({equipmentTypeId: selectedOption ? selectedOption.value : null});
                break;
            case "equipment":
                this.setState({equipment: selectedOption.target.value});
                break;
            case "systemId":
                this.setState({systemId: selectedOption ? selectedOption.value : null});
                break;
            case "problemTypeId":
                this.setState({problemTypeId: selectedOption ? selectedOption.value : null});
                break;
            case "fromDate":
                this.setState({fromDate: selectedOption ? selectedOption : null});
                break;
            case "toDate":
                this.setState({toDate: selectedOption ? selectedOption : null});
                break;
            default:
                break;
        }
    }

    handleSearchACRClic = (event) => {
        
        let filters = {
            companyId:          this.state.companyId,
            regionId:           this.state.regionId,
            activoId:           this.state.activoId,
            processCenterId:    this.state.processCenterId,
            installationId:     this.state.installationId,
            equipmentTypeId:    this.state.equipmentTypeId,
            equipmentKey:       this.state.equipment,
            systemCatId:        this.state.systemId,
            problemTypeId:      this.state.problemTypeId,
            from:               this.state.fromDate,
            to:                 this.state.toDate,
        };

        this.props.doFilterList(filters);
    }

    setInputValue(field) {
        let opt = null;
        let mapped = null;

        switch (field) {
            case "companyId":
                opt = this.state.companies.filter(option => option.id === this.state.companyId);
                break;
            case "regionId":
                opt = this.state.regions.filter(option => option.id === this.state.regionId);
                break;
            case "activoId":
                opt = this.state.activos.filter(option => option.id === this.state.activoId);
                break;
            case "processCenterId":
                opt = this.state.processCenters.filter(option => option.id === this.state.processCenterId);
                break;
            case "installationId":
                opt = this.state.installations.filter(option => option.id === this.state.installationId);
                break;
            case "equipmentTypeId":
                opt = this.state.equipmentTypes.filter(option => option.id === this.state.equipmentTypeId);
                break;
            // case "equipment":
            //     opt = this.state.activos.filter(option => option.id === this.state.activoId);
            //     break;
            case "systemId":
                opt = this.state.systems.filter(option => option.id === this.state.systemId);
                break;
            case "problemTypeId":
                opt = this.state.problemTypes.filter(option => option.id === this.state.problemTypeId);
                break;
            default:
                break;
        }

        if(opt){
            mapped = opt.map( (item, i) => { return {value: item.id, label: item.description}});
        }
        return mapped;
        
    }
    
    render() {

        const {
            isEquipmentTypeDisabled,
            isSystemDisabled,
        } = this.state;

        return (
            <AvForm onValidSubmit={this.handleSearchACRClic}>
                <Row>
                    <Col sm={6}>
                        <AvGroup>
                            <FormGroup row >
                                <Label for="company" sm={4} >
                                    Compaa{' '}<Tooltip tt={searchForm.company} target="company"/>
                                </Label>
                                <Col sm={8}>
                                    <Select 
                                        noOptionsMessage={() => "No hay opciones"}
                                        loadingMessage={() => "Cargando..."}
                                        id="company" 
                                        name="company" 
                                        bsSize="sm" 
                                        sm={7}
                                        className={"react-select "+ (!this.state.companyIdIsValid?"is-invalid": "")}
                                        classNamePrefix="react-select"
                                        placeholder="Seleccione una opcin"
                                        options={ this.state.companies.map((item, i) => { return ( {value: item.id, label: item.description} ); })}
                                        isClearable={true}
                                        isSearchable={true}
                                        isLoading={this.state.companiesIsLoading}
                                        value={ this.setInputValue('companyId') }
                                        onChange={evt => this.handleInputChange(evt, 'companyId')}
                                        >
                                    </Select>
                                    <AvInput
                                        type="hidden"
                                        name="companyValue"
                                        id="companyValue"
                                        value={ this.setInputValue('companyId') }
                                        required
                                    />
                                    <AvFeedback>Debe seleccionar una compaa</AvFeedback>
                                </Col>
                            </FormGroup>
                        </AvGroup>

                        <FormGroup row >
                            <Label for="region" sm={4} >
                                Divisin/Regin{' '}<Tooltip tt={searchForm.region} target="region"/>
                            </Label>
                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                    loadingMessage={() => "Cargando..."}
                                    id="region"
                                    name="region"
                                    bsSize="sm"
                                    sm={7}
                                    className="react-select"
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.regions.map((item, i) => { return ({ value: item.id, label: item.description }); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isLoading={this.state.regionsIsLoading}
                                    value={ this.setInputValue('regionId') }
                                    onChange={evt => this.handleInputChange(evt, 'regionId')}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row >
                            <Label for="activo" sm={4} >
                                Activo/Gerencia{' '}<Tooltip tt={searchForm.activo} target="activo"/>
                            </Label>
                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                    loadingMessage={() => "Cargando..."}
                                    id="activo"
                                    name="activo"
                                    bsSize="sm"
                                    sm={7}
                                    className="react-select"
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.activos.map((item, i) => { return ({ value: item.id, label: item.description }); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isLoading={this.state.activosIsLoading}
                                    value={ this.setInputValue('activoId') }
                                    onChange={evt => this.handleInputChange(evt, 'activoId')}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row>
                            <Label for="processCenter" sm={4} >
                                U.O./Centro de Proceso{' '}<Tooltip tt={searchForm.processCenter} target="processCenter"/>
                            </Label>

                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                    loadingMessage={() => "Cargando..."}
                                    id="processCenter"
                                    name="processCenter"
                                    bsSize="sm"
                                    sm={7}
                                    className="react-select"
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.processCenters.map((item, i) => { return ({ value: item.id, label: item.description }); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isLoading={this.state.processCentersIsLoading}
                                    value={ this.setInputValue('processCenterId') }
                                    onChange={evt => this.handleInputChange(evt, 'processCenterId')}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row>
                            <Label for="installation" sm={4} >
                                Instalacin{' '}<Tooltip tt={searchForm.installation} target="installation"/>
                            </Label>
                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                    loadingMessage={() => "Cargando..."}
                                    id="installation"
                                    name="installation"
                                    bsSize="sm"
                                    sm={7}
                                    className="react-select"
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.installations.map((item, i) => { return ({ value: item.id, label: item.description }); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isLoading={this.state.installationsIsLoading}
                                    value={ this.setInputValue('installationId') }
                                    onChange={evt => this.handleInputChange(evt, 'installationId')}
                                    >
                                </Select>
                            </Col>
                            
                        </FormGroup>
                    </Col>

                    <Col sm={6}>
                        <FormGroup row >
                            <Col sm={4}>
                                <div >
                                    <CustomInput
                                        type="switch"
                                        id="chkEquipmentType"
                                        name="chkEquipmentType"
                                        label=" Tipo de Equipo" 
                                        inline
                                        onChange={this.toggleEquipmentType}
                                    />
                                    {' '}<Tooltip tt={searchForm.equipmentType} target="equipmentType"/>
                                </div>
                            </Col>

                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                        loadingMessage={() => "Cargando..."}
                                    id="equipmentType" 
                                    name="equipmentType" 
                                    bsSize="sm" 
                                    sm={7}
                                    className={"react-select " + isEquipmentTypeDisabled? "disabled" : ""}
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.equipmentTypes.map((item, i) => { return ( {value: item.id, label: item.description} ); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isDisabled={isEquipmentTypeDisabled}
                                    isLoading={this.state.equipmentTypesIsLoading}
                                    value={ this.setInputValue('equipmentTypeId') }
                                    onChange={evt => this.handleInputChange(evt, "equipmentTypeId")}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row >
                            <Label for="equipment" sm={4} >
                                Equipo{' '}<Tooltip tt={searchForm.equipment} target="equipment"/>
                            </Label>

                            <Col sm={8}>
                                <Input 
                                    type="text" 
                                    name="equipment" 
                                    id="equipment" 
                                    placeholder="Nmero de equipo"
                                    onChange={evt => this.handleInputChange(evt, 'equipment')}
                                    value={this.state.equipment || ''}
                                />
                            </Col>
                        </FormGroup>

                        <FormGroup row >
                            <Col sm={4}>
                                <div >
                                    <CustomInput
                                        type="switch"
                                        id="chkSystem"
                                        name="chkSystem"
                                        label=" Sistema" 
                                        inline
                                        onChange={this.toggleSystem}
                                    />
                                    {' '}<Tooltip tt={searchForm.system} target="system"/>
                                </div>
                            </Col>

                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                        loadingMessage={() => "Cargando..."}
                                    id="system" 
                                    name="system" 
                                    bsSize="sm" 
                                    sm={7}
                                    className={"react-select " + isSystemDisabled? "disabled" : ""}
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.systems.map((item, i) => { return ( {value: item.id, label: item.description} ); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isDisabled={isSystemDisabled}
                                    isLoading={this.state.systemsIsLoading}
                                    value={ this.setInputValue('systemId') }
                                    onChange={evt => this.handleInputChange(evt, "systemId")}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row >
                            <Label for="problemType" sm={4} >
                                Tipo de Problema{' '}<Tooltip tt={searchForm.problemType} target="problemType"/>
                            </Label>

                            <Col sm={8}>
                                <Select 
                                    noOptionsMessage={() => "No hay opciones"}
                                        loadingMessage={() => "Cargando..."}
                                    id="problemType" 
                                    name="problemType" 
                                    bsSize="sm" 
                                    sm={7}
                                    className="react-select"
                                    classNamePrefix="react-select"
                                    placeholder="Seleccione una opcin"
                                    options={ this.state.problemTypes.map((item, i) => { return ( {value: item.id, label: item.description} ); })}
                                    isClearable={true}
                                    isSearchable={true}
                                    isLoading={this.state.problemTypesIsLoading}
                                    value={ this.setInputValue('problemTypeId') }
                                    onChange={evt => this.handleInputChange(evt, "problemTypeId")}
                                    >
                                </Select>
                            </Col>
                        </FormGroup>

                        <FormGroup row >
                            <Label for="analisysStartDate" sm={4} >
                                Fecha de Anlisis{' '}<Tooltip tt={searchForm.analisysDate} target="analisysDate"/>
                            </Label>
                            <Col sm={4}>
                                <HyperDatepicker 
                                    id="fromDate"
                                    name="fromDate" 
                                    hideAddon={true}
                                    startDate={ this.state.fromDate }
                                    onChange2={(evt) => this.handleInputChange(evt, "fromDate")}
                                    isClearable={true}
                                    />
                            </Col>
                            <Col sm={4}>
                                <HyperDatepicker 
                                    id="toDate"
                                    name="toDate" 
                                    hideAddon={true}
                                    startDate={ this.state.toDate }
                                    onChange2={(evt) => this.handleInputChange(evt, "toDate")}
                                    isClearable={true}
                                    />
                            </Col>
                        </FormGroup>

                    </Col>
                </Row>
                <Button color="primary" className="float-right" > Buscar</Button>
                {/* onClick={this.handleSearchACRClic} */}
            </AvForm>
        );
    }
}

export default SearchForm;